import { StrengthKey } from '../types';

export interface UserTypeInfo {
  id: string;
  icon: string;
  name: string;
  catchCopy: string;
}

/*   10  */
const pairs: Record<string, StrengthKey[]> = {
  ORCH: ['AIDIR', 'TOOLC'],
  SPARK: ['IDEAS', 'AMBIG'],
  BRIDGE: ['EMBRI', 'FLOWM'],
  CRAFT: ['DATAR', 'FOCUS'],
  SPRINT: ['ANTIF', 'MICRO']
};

const meta: Record<string, UserTypeInfo> = {
  ORCH: { id: 'ORCH', icon: '', name: 'AI', catchCopy: ' ""' },
  SPARK: { id: 'SPARK', icon: '', name: '', catchCopy: ' ""' },
  BRIDGE: { id: 'BRIDGE', icon: '', name: '', catchCopy: 'C-3PO ""' },
  CRAFT: { id: 'CRAFT', icon: '', name: '', catchCopy: 'R2-D2 ""' },
  SPRINT: { id: 'SPRINT', icon: '', name: '', catchCopy: ' ""' },
  BALANCE: { id: 'BALANCE', icon: '', name: '', catchCopy: ' ""' }
};

export function classifyType(score: Record<StrengthKey, number>): UserTypeInfo {
  const avg: Record<string, number> = {};
  Object.entries(pairs).forEach(([k,[a,b]]) => {
    avg[k] = (score[a] + score[b]) / 2;
  });

  const [top, second] = Object.entries(avg).sort(([,x],[,y])=>y-x);
  return (top[1] - second[1] >= 1.5) ? meta[top[0]] : meta['BALANCE'];
}
